<template>
  <v-app-bar
    app
    color="primary"
    dark
  >
    <div class="d-flex align-center">
      <v-img
        alt="Vuetify Logo"
        class="shrink mr-2"
        contain
        src="https://cdn1.sportngin.com/attachments/touch_icon_graphic/9218-121194344/rccl-logo-2019-600px_size_120.png"
        transition="scale-transition"
        width="40"
      />
      <v-toolbar-title class="headline font-weight-black">{{ 'RHINO CUP' }}</v-toolbar-title>
    </div>

    <v-spacer></v-spacer>

    <v-menu offset-y>
      <template v-slot:activator="{ on }">
        <v-btn
          text
          color
          v-on="on"
        >
          <v-icon
            v-if="currentLeague"
            left
            color=""
          >{{ currentLeague.icon }}</v-icon>
          <v-icon
            right
            color=""
          >mdi-menu-down</v-icon>
        </v-btn>
      </template>
      <v-list dense>
        <v-list-item
          v-for="(league, index) in leagues"
          :key="`${index}010`"
          @click="changeLocale(league)"
        >
          <v-list-item-title class="primary--text">
            <v-icon
              color="primary"
            >{{ league.icon }}</v-icon>
            <span>{{ league.type }}</span>
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <v-menu
        bottom
        origin="center center"
        transition="scale-transition"
        offset-y
      >
        <template v-slot:activator="{ on }">
          <v-btn
            icon
            large
            v-on="on"
            color="primary"
          >
            <v-avatar
              size="32px"
              item
            >
              <img
                src="avatar.png"
                alt
              />
            </v-avatar>
          </v-btn>
        </template>

        <v-card>
          <v-list>
            <v-list-item>
              <v-list-item-avatar>
                <img
                  src="avatar.png"
                />
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title>WFF</v-list-item-title>

                <v-list-item-subtitle>{{ }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-list>

          <v-divider></v-divider>

          <v-list>
            <v-list-item
              router
            >
              <v-list-item-action>
                <v-icon class="primary--text">mdi-account-circle</v-icon>
              </v-list-item-action>
              <v-list-item-title>Perfil</v-list-item-title>
            </v-list-item>
            <v-list-item @click.prevent="logOut">
              <v-list-item-action>
                <v-icon class="primary--text">mdi-logout-variant</v-icon>
              </v-list-item-action>
              <v-list-item-title>Log out</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-card>
      </v-menu>
  </v-app-bar>
</template>

<script>
import firebase from "@/firebaseConfig";
import listTeams from "@/libary/teams";
import listWTeams from "@/libary/wteams";
export default {
  data: () => ({
    currentLeague: null,
    leagues: [
      {
        type: 'MANS',
        icon: 'mdi-human-male',
        list: 'players',
        teams: listTeams
      },
      {
        type: 'WOMANS',
        icon: 'mdi-human-female',
        list: 'wplayers',
        teams: listWTeams
      }
    ]
  }),
  created () {
    this.currentLeague = this.leagues[0];
  },
  methods: {
    changeLocale (league) {
      this.currentLeague = league;
      // eslint-disable-next-line no-undef
      Fire.$emit('leagueChanged', ({ currentLeague: this.currentLeague }));
    },
    logOut () {
      const vm = this;
      firebase.auth().signOut().then(function() {
        vm.$router.push(vm.$route.query.redirect || '/login')
      }, function(error) {
        console.error('Sign Out Error', error);
      });
    }
  },
  mounted () {
    this.currentLeague = this.leagues[0];
    // eslint-disable-next-line no-undef
      Fire.$emit('leagueChanged', ({ currentLeague: this.currentLeague }));
  }
}
</script>